@import url('https://fonts.googleapis.com/css2?family=Heebo:wght@700&family=Raleway:wght@500&display=swap');

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;

}

.app {
	min-height: 100vh;
	padding: 7rem 0;
  background: rgb(58,180,120);
  background: linear-gradient(90deg, rgba(58,180,120,1) 0%, rgba(29,253,49,1) 50%, rgba(69,252,116,1) 100%);

	display: flex;
	justify-content: center;
}

.card {
	background-color: #fff;

	border: 2px solid rgb(226, 226, 226);

	padding: 40px 25px;

	width: 100%;
	max-width: 600px;

	border-radius: 10px;

	margin-bottom: 50px;

  text-align: center;


}

@media only screen and (min-width: 850px) {
  .card {
    background-color: #fff;

    border: 2px solid rgb(226, 226, 226);
  
    padding: 40px 25px;
  
    width: 100%;
    max-width: 600px;
  
    border-radius: 10px;
  
    margin-bottom: 50px;
  
    text-align: center;

    margin-inline-end: 10px;
    margin-inline-start: 50px;
  
    display: inline-table;
  

    
  }
}

.card .card-body h1{
	font-family: Heebo;
	font-size: 2.6rem;
	margin-bottom: 10px;
}

.line {
	width: 100%;
	height: 2px;

	border: 1px solid rgb(218, 218, 218);
}

.card .card-body h2{
	font-family: Raleway;
	font-size: 1.4rem;

	margin-top: 15px;
}

.loading {
	height: 100vh;
	display:flex;
	align-items: center;
	justify-content: center
}

